@tailwind base;
@tailwind components;
@tailwind utilities;

/* .top-navigation .active {
  background-color: #c3e3ff;
} */
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;1,200;1,400;1,700&display=swap");
body {
  font-family: "Nunito Sans", sans-serif;
}

.action-button .active {
  background-color: #c3e3ff;
}

.side-navigation-container {
  @apply pl-2;
}

.side-navigation-container .navigation-item {
  @apply mb-1 text-gray-500 hover:text-blue-600 rounded-lg flex items-center p-2;
}

.side-navigation-container .active {
  @apply bg-blue-600 font-bold border border-blue-800 hover:text-white text-white p-2;
}

.container-section {
  @apply dark:bg-dark-800 border dark:border-blue-300 dark:border-opacity-20 rounded dark:text-white bg-white;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.loading-animate {
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.main-side-spacer {
  @apply lg:w-52 w-0;
}

.z-60 {
  z-index: 60;
}

.height-calculated {
  height: calc(100vh - 3rem) !important;
}

.bg-body-color {
  background-color: #e9f2fe;
}

.width-transition {
  transition: width 0.3s;
}

.modal-component {
  /* width: 100%;
  height: 100%; */
  max-width: 90%;
  max-height: 90%;
  height: fit-content;
  margin: auto;
}

.gradient-animation {
  background: linear-gradient(
    -45deg,
    rgb(255, 255, 255),
    rgb(204, 204, 204),
    rgb(187, 187, 187),
    rgb(255, 255, 255)
  );
  background-size: 400% 400%;
  animation: gradient 5s ease infinite;
  /* height: 100vh; */
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
